:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
@keyframes twinkle {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(59, 130, 246, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.8);
  }
}
.star {
  animation: twinkle 3s ease-in-out infinite;
}
.star:nth-child(odd) {
  animation-delay: 1s;
}
.star:nth-child(3n) {
  animation-delay: 2s;
}
.float-animation {
  animation: float 6s ease-in-out infinite;
}
.glow-effect {
  animation: pulse-glow 2s ease-in-out infinite;
}
.starfield-bg {
  background: #000000;
}
.deep-blue-bg {
  background: linear-gradient(180deg, #0B1B3B 0%, #08142C 100%);
}
.star--sm {
  width: 1.5px;
  height: 1.5px;
}
.star--md {
  width: 2.5px;
  height: 2.5px;
}
.star--lg {
  width: 3.5px;
  height: 3.5px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.9);
}
.star--cold {
  background: rgba(190, 210, 255, 1);
}
.star--neutral {
  background: rgba(255, 255, 255, 1);
}
.star--warm {
  background: rgba(255, 230, 200, 1);
}
::-webkit-scrollbar {
  width: 4px;
}
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 2px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}
.glass-effect {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}
.gradient-bg {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.1) 0%, rgba(147, 51, 234, 0.1) 100%);
}
.frosted-surface {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  background: rgba(255, 255, 255, 0.4);
}
.frosted-card {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  background: radial-gradient(140% 120% at 10% 0%, rgba(255,255,255,0.06), transparent 50%),
    radial-gradient(120% 120% at 90% 0%, rgba(102, 198, 255, 0.08), transparent 60%),
    rgba(255,255,255,0.06);
}
.calendar-sans {
  font-family: system-ui,
    -apple-system,
    "Segoe UI",
    Roboto,
    "Helvetica Neue",
    "Noto Sans",
    Ubuntu,
    Cantarell,
    Arial,
    sans-serif;
  font-weight: 600;
}
.calendar-gradient-neo {
  background-image: radial-gradient(420px 300px at 0% 0%, rgba(26, 214, 247, 0.05), transparent 60%),
    radial-gradient(380px 260px at 100% 100%, rgba(120, 234, 255, 0.03), transparent 65%),
    
    repeating-linear-gradient(45deg,
      rgba(255,255,255,0.02) 0px,
      rgba(255,255,255,0.02) 1px,
